import React, { useState } from "react";
import Category from "./components/Category.js";
import ResultLists from "./components/ResultLists.js";
import Information from "./components/Information.js";
import "./App.css";

function App() {
  const [selectedKeywords, setKeyword] = useState([]);

  const category = [
    { id: 1, title: "", sub: ["2022", ""] },
    {
      id: 2,
      title: "",
      sub: ["", "", "", "", "", ""],
    },
    {
      id: 3,
      title: "",
      sub: ["---", "-"],
    },
    {
      id: 4,
      title: "",
      sub: [
        "",
        "",
        "",
        "",
        "",
      ],
    },
    {
      id: 5,
      title: "",
      sub: ["1", "1", "6", "3", "1"],
    },
    { id: 6, title: "", sub: ["A6", "B6", "A5", "B5", "A4"] },
    {
      id: 7,
      title: "",
      sub: ["", "", "PVC", ""],
    },
    { id: 8, title: "", sub: ["/", "6", "", ""] },
    { id: 8, title: "", sub: ["100g", "120g"] },
    {
      id: 9,
      title: "",
      sub: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
      ],
    },
  ];

  //   
  function handleKeywords(newKeyword) {
    setKeyword(newKeyword);
  }

  //  4  
  let result = "";
  function checkNumber() {
    if (selectedKeywords.length < 4) {
      return (
        <div className="keywordMessage">    </div>
      );
    } else {
      return (
        <ResultLists
          selectedKeywords={selectedKeywords}
          onChange={(selectedKeyword) => handleKeywords(selectedKeyword)}
        ></ResultLists>
      );
    }
  }
  result = checkNumber();
  const length = Information.length
  return (
    <div className="App">
      <div className="titleDiv">
      <div className="title">
        <span id="emoji"></span>
        <span id="appTitle"> </span>
      </div>
      <span className="info">{length}    (2022.12.22)</span>
      <a
        class="info"
        href="https://joy-lee.notion.site/b4d709f83ebe436d84599e3e99d8fbab"
        target="_blank"
        rel="noopener noreferrer"> !</a>
      </div>
      <Category
        data={category}
        selectedKeywords={selectedKeywords}
        onChange={(selectedKeyword) => handleKeywords(selectedKeyword)}
      ></Category>
      {result}
    </div>
  );
}

export default App;
